.subpage {
  z-index: 5;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: white;
  margin: 0 auto;
  background-image: url("icons/fcc_bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position-x: center; }
  .subpage h1:first-of-type {
    margin-top: 0; }
  .subpage .page {
    position: relative;
    scroll-behavior: smooth;
    overflow-x: hidden;
    overflow-y: scroll;
    width: 100%;
    height: 100vh; }

.page .content {
  width: 45em;
  max-width: 90%;
  margin: 1em auto 8em auto; }
  .page .content .links a + a {
    margin-left: 1em; }
  .page .content img {
    max-width: 100%;
    max-height: 50vh;
    display: block;
    margin: 4em auto; }
    .page .content img.sm-bottom-margin {
      margin-bottom: 1em; }
    .page .content img.no-bottom-margin {
      margin-bottom: 0; }
      .page .content img.no-bottom-margin + h2 {
        margin-top: 1em; }
  .page .content h2 + img {
    margin-top: 0; }
  .page .content .attention-box {
    border-color: #0053a0;
    border-width: 2px;
    border-style: solid;
    margin: 2em auto 3em auto;
    padding: 1em; }
    .page .content .attention-box hr {
      width: 70%;
      border: none;
      background-color: black;
      height: .5px; }
    .page .content .attention-box h3 {
      display: block;
      text-transform: uppercase;
      text-align: center;
      margin: 0;
      margin-bottom: 1em; }
    @media screen and (min-width: 1000px) and (min-aspect-ratio: 10 / 7) {
      .page .content .attention-box {
        max-width: 30em; } }
  .page .content .emphasis {
    font-style: italic;
    font-weight: bold;
    color: gray;
    font-size: 1.5em;
    display: block;
    margin: 0 auto;
    text-align: center;
    margin: 2em auto; }
    .page .content .emphasis .bigger {
      font-size: 1.5em;
      color: #d40f41; }
  .page .content .two-content {
    margin: 0 auto; }
    @media screen and (min-width: 800px) {
      .page .content .two-content {
        display: flex;
        justify-content: space-between;
        align-items: flex-start; }
        .page .content .two-content.reverse {
          flex-direction: row-reverse; }
        .page .content .two-content h1, .page .content .two-content h2, .page .content .two-content h3 {
          margin-top: 0; }
        .page .content .two-content p:first-of-type {
          margin-top: 0; }
        .page .content .two-content img {
          margin: 1em; } }

.back-arrow {
  position: absolute;
  top: .5em;
  right: .5em;
  cursor: pointer;
  text-decoration: none;
  z-index: 7; }
  .back-arrow img {
    max-width: 2em; }

@media screen and (min-width: 1000px) and (min-aspect-ratio: 10 / 7) {
  .page .content {
    position: relative;
    right: 10%; }
  .back-arrow {
    right: auto;
    left: .5em;
    top: .5em; } }

html {
  margin: 0;
  overflow-x: hidden;
  padding: 0;
  background: gray; }

body {
  margin: 0 auto;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 18px;
  position: relative;
  scroll-behavior: smooth;
  height: 100vh;
  overflow: hidden; }
  @media screen and (min-width: 2048px) {
    body {
      font-size: 1.5em; } }
  @media screen and (min-width: 2560px) {
    body {
      font-size: 1.8em; } }
  @media screen and (min-width: 3840px) {
    body {
      font-size: 3em; } }
  @media screen and (min-width: 5000px) {
    body {
      font-size: 4em; } }
  @media screen and (min-width: 6000px) {
    body {
      font-size: 5em; } }
  @media screen and (min-width: 7680px) {
    body {
      font-size: 6em; } }

.main {
  position: relative;
  scroll-behavior: smooth;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: scroll; }

h1 {
  color: #d40f41;
  font-size: 2.5em; }

h2 {
  color: #d40f41;
  font-size: 2em; }

h1, h3 {
  margin-top: 2em; }

h2 {
  margin-top: 1em;
  margin-bottom: .5em; }

.block {
  display: block;
  margin: .5em auto; }

.mt {
  margin-top: 1em; }

.mt2 {
  margin-top: 2em; }

a {
  color: #0053a0; }
  a:visited {
    color: #0053a0; }
  a:hover {
    color: #2194ff; }

a.no-underline {
  text-decoration: none; }

button {
  display: block;
  margin: .5em auto;
  background-color: #0053a0;
  color: white;
  font-size: 1em;
  box-shadow: none;
  border: 1px solid white;
  padding: .5em;
  min-width: 8em;
  cursor: pointer;
  border-radius: .1em;
  transition: filter .2s ease, transform .2s ease; }
  button:hover {
    filter: brightness(1.3); }
  button.red {
    background-color: #d40f41; }
  button:active, button:focus {
    outline: none; }
  button.selected {
    cursor: default;
    filter: brightness(0.9);
    transform: scale(0.9); }

.centered {
  text-align: center; }

.right-align {
  text-align: right; }

.small {
  font-size: .8em;
  margin: 0; }

.margin-border {
  margin: 6em 1em 1em 1em; }
  @media screen and (min-width: 500px) {
    .margin-border {
      margin: 1em; } }

img.bg {
  display: none; }
  @media screen and (min-width: 350px) {
    img.bg {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
