      min-width: 100%;
      height: 200%;
      opacity: .2;
      z-index: -1;
      pointer-events: none; } }

.loader {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%; }
  .loader img {
    width: 5em;
    border-radius: 50%;
