  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 11;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.9);
  width: 100%;
  height: 100vh; }
  .modal .inner {
    border-radius: .1em;
    background-color: white;
    padding: 1em;
    max-width: 90%;
    width: 50em;
    box-sizing: border-box; }

.close {
